.dropdown-menu-right {
  right: 0;
  left: auto;
}

.center-box {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.center-box .modal-dialog {
  min-width: 45%;
}

#accountDropdown {
  transition: all .2s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

#mailsDropdown {
  margin-top: 3px;
  margin-right: 10px;
}

.hidden {
  transform: translateY(-100px);
  opacity: 0;
  z-index: -100;
  margin-bottom: -50px;
}

.notification {
  transition: all .3s ease-in-out;
}

.navbar {
  z-index: 100;
  background-color: black;
}

.mailBubble {
  background-color: red;
  border-radius: 25%;
  padding: 1px 2px;
  font-size: 10px;
  color: white;
  position: absolute;
  top: 0;
  right: 0;
}

.profilePicture {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: white;
  display: inline-block;
  vertical-align: middle;
  border: 2px solid white;
}

body {
  overflow-y: hidden;
  padding-top: 62px;
}

.bodyWrapper {
  min-height: 100%;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  padding: 62px 0 0 0;
}

.sidebarWrapper {
  height: 100%;
  padding: 0;
  position: fixed;
}

#sidebar {
  height: 100%;
  background-color: #3d3d3d;
  color: white;
  position: relative;
  overflow: hidden;
}

#sidebar ul {
  list-style-type: none;
  padding: 0;
}

.searchBox {
  padding: 0 15px 15px 15px;
}

.searchBox .input-group-addon {
  background-color: white;
  padding: 0 5px 0 5px;
}

.searchBox input, .input-group-addon, input:active, .input-group-addon:active {
  border-width: 0;
  outline: none !important;
}

.sidebarLogo {
  padding: 15px 15px 15px 15px;
}

.sidebarSectionHeader {
  background-color: #2c2c2c;
  padding: 8px;
}

.sidebarSection {
  margin: 0;
}

.sidebarSection a li {
  padding: 8px;
}

.sidebarSection a, a:focus {
  color: white;
}

.sidebarSection a li:hover {
  background-color: #383637;
}

.active {
  background-color: #383637;
  border-left: 4px solid white;
}

.sidebarSection a, a:hover, a:focus {
  text-decoration: none;
}

.sidebarSection a li img {
  width: 20px;
  height: 20px;
  margin: 0 5px 0 5px;
}

.sidebarImportance li {
  padding: 8px;
}

.importanceBubble {
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-radius: 50%;
  margin: 0 5px 0 5px;
  display: inline-block;
  vertical-align: middle;
}

.contentWrapper {
  height: 100%;
  padding: 0;
  overflow-y: auto;
}

.content {
  padding: 25px 25px 0 25px;
  position: relative;
  word-wrap: break-word;
  /*delete if necessary, used coz json is too long*/
  height: 100%;
  overflow-y: auto;
}

@media (min-width: 992px) {
  .contentWrapper {
    float: right;
  }
  .mobileNavbar {
    display: none;
  }
}

@media (max-width: 992px) {
  #sidebar {
    display: none;
  }
  .desktopNavbar {
    display: none;
  }
}

.navbar-toggler {
  border: none;
  font-size: 1rem;
}

#navbarMobileContent ul li a span {
  vertical-align: middle;
}

#navbarMobileContent ul li a img {
  width: 20px;
  vertical-align: middle;
  margin: 0 5px 0 5px;
}

#ajax-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
}

#ajax-loading img {
  width: 50px;
  height: 50px;
  display: block;
}

.content-card {
  height: 55vh;
}

.content-card .category-wrapper {
  overflow: scroll;
}

.category-cards {
  height: 40vh;
}

.category-cards .category-wrapper {
  overflow: scroll;
}

.category-cards .category-wrapper .category-three {
  height: 100%;
}

.content-card .card-body {
  overflow-: scroll;
}

.noscroll-bar::-webkit-scrollbar {
  display: none;
}

.list-group-item div {
  width: 50%;
  float: left;
  display: flex;
  align-items: center;
}

.btn {
  cursor: pointer !important;
}

.image-scroller .image-group img {
  width: 100%;
}

/* Transactions */
.tabs-wrapper {
  height: 160px;
}

.tabs-wrapper .tab {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  margin-top: 20px;
  transition: all .3s ease-in-out;
}

.tabs-wrapper .tab .tab-border {
  margin-top: 10px;
  width: 90%;
  border-bottom: #000 solid 2px;
  transition: all .3s ease-in-out;
}

.tabs-wrapper .tab .active {
  border-bottom: #ccc solid 2px;
}

.tabs-wrapper .tab h3 {
  font-size: 21px;
  font-weight: 300;
  cursor: pointer;
}

.tabs-wrapper .tab:hover > .tab-border {
  border-bottom: #eee solid 2px;
}

/* Web Configuration */
.freatured-product {
  height: 100%;
}

.modal-body {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 60vw;
  max-height: 60vh;
}

.single-featured {
  text-align: center;
}

.single-featured img {
  max-width: 100%;
}

/* Media */
